import { Order } from '../Order.js'
export default {
  data () {
    return {
      searchText:""
    }
  },
  methods: {
    clear () {
      this.searchText = ''
    }
  },
  watch: {
    searchText(val) {
      Order.$emit('Toast', val)
    }
  }
}



